"use client"


import { useEffect, useState } from "react"
import Link from 'next/link'
import { useRouter } from "next/navigation"
import Api from "../services/logic"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Edit, Trash, TrendingUp, TrendingDown, Wallet } from "lucide-react"
import { Transaction, Category, SortOptions } from '../services/logic'


const api = new Api()




export default function AmountsTable(  
) {
  const [transactions, setTransactions] = useState<Transaction[]>([])
  const [categories, setCategories] = useState<Category[]>([])
  const [loading, setLoading] = useState(false)
  const [page, setPage] = useState(1)
  const [pageSize, setPageSize] = useState(1000)
  const [filters, setFilters] = useState<{ category?: string; type?: string; description?: string }>({})

  const router = useRouter();

  useEffect(() => {
    async function fetchCategories() {
      try {
        const cats = await api.getAllCategories()
        if (Array.isArray(cats)) {
          setCategories(cats)
          console.log('Loaded categories:', cats)
        } else {
          console.warn('Categories is not an array:', cats)
          setCategories([])
        }
      } catch (error) {
        console.error('Error loading categories:', error)
        setCategories([])
      }
    }
    fetchCategories()
  }, [])

  useEffect(() => {
    fetchTransactions()
  }, [page, pageSize, filters])

  async function fetchTransactions() {
    setLoading(true)
    try {
      const result = await api.getTransactions()

      setTransactions(result.data || [])
    } finally {
      setLoading(false)
    }
  }

  function getCategoryType(type: "expense" | "income" | string) {
    const expenses = transactions.filter(t => t.type === "expense" || t.type === "expence");
    const incomes = transactions.filter(t => t.type === "income");

    const expenseSum = expenses.reduce((sum, transaction) => sum + Math.abs(transaction.amount), 0);
    const incomeSum = incomes.reduce((sum, transaction) => sum + Math.abs(transaction.amount), 0);

    const totalAmount = Math.abs(incomeSum) - Math.abs(expenseSum) ;

    return {
        expenseTotal: expenseSum,
        incomeTotal: incomeSum,
        total: totalAmount
      };
  }

  const totals = getCategoryType("all");


 
  return (
    <div className="space-y-10 px-10">
      {/* Summary Cards */}
      <div className="grid grid-cols-1 md:grid-row-3 gap-4 md:gap-8">
        {/* Total Income Card */}
        <Card className="min-w-xs md:max-w-3xs bg-green-50 dark:bg-green-900/20 border-green-200 dark:border-green-800">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-green-700 dark:text-green-400">
              Total Income
            </CardTitle>
            <TrendingUp className="h-4 w-4 text-green-600 dark:text-green-400" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-700 dark:text-green-400">
              +{totals.incomeTotal.toFixed(1)} BYN
            </div>
            <p className="text-xs text-green-600 dark:text-green-300">
              From {transactions.filter(t => t.type === "income").length} transactions
            </p>
          </CardContent>
        </Card>

        {/* Total Expenses Card */}
        <Card className="min-w-xs max-w-3xs bg-red-50 dark:bg-red-900/20 border-red-200 dark:border-red-800">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-red-700 dark:text-red-400">
              Total Expenses
            </CardTitle>
            <TrendingDown className="h-4 w-4 text-red-600 dark:text-red-400" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-700 dark:text-red-400">
              -{totals.expenseTotal.toFixed(1)} BYN
            </div>
            <p className="text-xs text-red-600 dark:text-red-300">
              From {transactions.filter(t => t.type === "expense" || t.type === "expence").length} transactions
            </p>
          </CardContent>
        </Card>

        {/* Net Balance Card */}
        <Card className="min-w-lg md:min-w-xs max-w-xs  md:max-w-2xs bg-blue-50 dark:bg-blue-900/20 border-blue-200 dark:border-blue-800">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-blue-700 dark:text-blue-400">
              Net Balance
            </CardTitle>
            <Wallet className="h-4 w-4 text-blue-600 dark:text-blue-400" />
          </CardHeader>
          <CardContent>
            <div className={`text-2xl font-bold ${
              totals.total >= 0 
                ? 'text-green-700 dark:text-green-400' 
                : 'text-red-700 dark:text-red-400'
            }`}>
              {(totals.total).toFixed(1)} BYN
            </div>
            <p className="text-xs text-blue-600 dark:text-blue-300">
              {totals.total >= 0 ? 'Positive' : 'Negative'} balance
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Your existing table and other components would go here */}
      <div>
        {/* Add your table component here when ready */}
      </div>
    </div>
  )
}